import { getGUID } from '@/utils'

(function($) {
  'use strict'
  if (!$.isFunction($.fn.findFilter)) {
    /**
     * @param filter
     * @param selector
     *  [data-filter] dom
     *
     * [data-filter="a"]
     * $.CurrentDialog.findFilter('a')
     * div[data-filter="a"]
     * $.CurrentDialog.findFilter('a', 'div')
     */
    $.fn.findFilter = function(filter, selector) {
      if (filter) {
        return this.find(`${selector || ''}[data-filter="${filter}"]`)
      }
      return undefined
    }
  }

  if (!$.getGUID) {
    /**
     * 
     * @returns {string}
     */
    $.getGUID = getGUID
  }
}(jQuery))
